// Переменные ////////////////////////////////////////////////////////////////////

$font-main: 'Kazimir',
Arial,
sans-serif;
$font-accent: 'Aestetika',
Arial,
sans-serif;
$mobile-width: 320px;
$mobile-width-wide: 374px;
$tablet-width: 550px;
$desktop-small-width: 790px;
$desktop-width: 1200px;
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
$white: #fdfdfd;
