$red: #dd5157;
$yellow: #fff22d;
$green: #5ea462;
$purple: #6f5cad;

// миксины mixins ////////////////////////////////////////////////////////////////////

@mixin animation {
  transition: 0.4s;

  &:hover {
    outline: none;
    opacity: 0.6;
  }

  &:focus,
  &:active {
    outline: none;
    opacity: 0.4;
  }
}

@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin text ($size:12px, $line:17px) {
  margin: 0;
  padding: 0;
  color: $black;
  font-family: $font-main;
  font-size: $size;
  font-weight: 500;
  line-height: $line;
}

@mixin close-btn {
  position: relative;
  width: 15px;
  height: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 14px;
    height: 1px;
    top: 50%;
    left: 0;
    background-color: $black;
    transform: translate(-50%);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover {
    outline: none;

    &::after,
    &::before {
      background-color: $red;
    }
  }

  &:focus,
  &:active {
    outline: none;

    &::after,
    &::before {
      background-color: $red;
      opacity: 0.6;
    }
  }
}


