// глобальные/global////////////////////////////////////////////////////////////////////
@font-face {
  font-family: 'Aestetika';
  src: url('./fonts/Aestetika.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Kazimir';
  src: url('./fonts/KazimirTextItalic.woff') format('woff');
  font-weight: 300;
  font-style: italic;
}

@font-face {
  font-family: 'Kazimir';
  src: url('./fonts/KazimirTextRegular.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

html,
body {
  box-sizing: border-box;
  max-width: 100%;
  min-width: $mobile-width;
  overflow-x: hidden;
  margin: 0;
  @include text;
  background-color: $white;
}

a,
button {
  color: $black;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  @include visually-hidden;
}

.opacity--0 {
  opacity: 0;
}

// Хедер шапка сайта /////////////////////////////////////////////
.wrap {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 25px;
  padding-right: 25px;
}

.header__wrap-fixed {
  min-height: 27px;
}

.header__menu {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 20px;
  transition: background-color 0.4s;

  &.fixed {
    width: auto;
    z-index: 2;
    position: fixed;
    top: 0;
    padding-top: 20px;
    margin-right: 25px;
    background-color: $white;
  }
}

.header__logo {
  @include animation;
  display: inline-block;
  text-decoration: none;
  margin-right: 20px;
  padding-left: 14px;
  border-bottom: 1px solid $black;
}

.header__logo-text {
  margin: 0;
  padding: 0;
  padding-bottom: 6px;
  text-transform: lowercase;
  font-family: $font-accent;
  font-size: 10px;
  line-height: 10px;
  font-weight: 700;
  color: $black;
}

.header__form {
  display: block;
  flex-basis: 100px;
  min-width: 100px;
  flex-grow: 1;
  padding: 0;
  margin: 0;
  margin-right: 20px;
}

.header__search {
  width: 100%;
  padding: 0;
  margin: 0;
}

.search {
  position: relative;
}

.search__label {
  position: absolute;
  bottom: 7px;
  right: 5px;

  font-family: $font-accent;
  font-size: 10px;
  line-height: 10px;
  font-weight: 700;
  transition: 0.4s;
}

.search__input {
  display: block;
  width: 100%;
  min-width: 100px;
  padding: 0;
  padding-left: 11px;
  padding-right: 20px;
  padding-bottom: 6px;
  margin: 0;
  border: 0;
  border-bottom: 1px solid $black;

  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  font-family: $font-accent;
  color: $black;

  &:hover {
    outline: none;
  }

  &:active,
  &:focus {
    outline: none;

    &+.search__label {
      opacity: 0;
      pointer-events: none;
    }

    &~.search__reset {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.search__reset {
  @include close-btn;
  position: absolute;
  bottom: 7px;
  right: 0;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
}

.header__lang {
  width: 20px;
  padding-bottom: 7px;
  border-bottom: 1px solid $black;

  text-decoration: none;
  text-align: center;
  font-size: 10px;
  line-height: 10px;
  font-weight: 700;
  font-family: $font-accent;
  color: $black;

  transition: 0.4s;

  &:hover {
    color: $red;
  }
}

.header__tags {
  margin-bottom: 20px;
  transition: 0.4s;
}

.tags {
  display: none;
  transition: 0.4;
  &.active {
    display: block;
  }
}

.tags__list {
  margin: 0;
  padding: 0;
  list-style: none;

}

.tags__item {
  display: inline-block;

  &.grant {
    span {
      color: $blue;
    }
  }

  &.project {
    span {
      color: $green;
    }
  }

  &.book {
    span {
      color: $purple;
    }
  }

  &.about {
    span {
      color: $yellow;
    }
  }

  &.active {
    span {
      color: $black;
    }
  }
}

.tags__link {
  color: $red;
  text-decoration: none;

  span {
    color: $red;
    transition: 0.4s;
  }

  &.active {
    span {
      color: $black;
    }
  }
}

