.article {
  margin-bottom: 20px;
  padding-bottom: 6px;
  border-bottom: 1px solid $black;

  &.grant {
    .article__figure {
      &::after {
        background-color: #3994fc;
        // background-color: $blue;
        opacity: 0.6;
      }
    }
  }

  &.project {
    .article__figure {
      &::after {
        background-color: $green;
        opacity: 0.6;
      }
    }
  }

  &.book {
    .article__figure {
      &::after {
        background-color: $purple;
        opacity: 0.6;
      }
    }
  }

  &.about {
    .article__figure {
      &::after {
        // background-color: $yellow;
        // opacity: 0.6;
        // background-color: rgba(255, 223, 45, 0.6);
        background-color: rgba(255, 200, 30, 0.6);
      }
    }
  }
}

.article__time {
  padding-left: 10px;
  font-style: normal;
  font-weight: 700;
  font-family: $font-accent;
  font-size: 10px;
  line-height: 10px;
}

.article__link {
  text-decoration: none;

  &:hover {
    .article__figcaption {
      opacity: 0;
    }

    .article__figure {
      img {
        filter: none;
        filter: none;
        opacity: 1;
      }

      &::after {
        opacity: 0;
      }
    }
  }
}

.article__figure {
  height: 140px;
  position: relative;
  margin: 0;
  padding: 0;
  overflow: hidden;

  img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    filter: grayscale(100%);
    filter: gray;
    opacity: 0.7;
    transition: 0.6s;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(255, 68, 68, 0.6);
    // background-color: $red;
    // opacity: 0.6;
    transition: 0.4s;
  }
}

.article__figcaption {
  position: relative;
  z-index: 1;
  padding-top: 12px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 12px;
  transition: 0.4s;
}

.article__author {
  color: $black;
  text-decoration: none;
  font-family: $font-accent;
  font-weight: 700;
  font-style: normal;
  font-size: 11px;
  line-height: 11px;
}

.article__title {
  margin: 0;
  padding: 0;
  color: $black;
  text-decoration: none;
  font-family: $font-accent;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  line-height: 15px;

  text-overflow: ellipsis;

  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}





